// Mobile toggle icon
.dswp-nav-mobile-toggle-icon {
    cursor: pointer;
    display: none;
    align-items: center;
    gap: 5px;
    padding: 8px;
    background: none;
    border: 1px solid currentColor;
    border-radius: 4px;
    color: inherit;
    transition: all 0.3s ease;

    &:hover {
        background-color: rgba(0, 0, 0, 0.05);
    }

    svg {
        width: 24px;
        height: 19px;
    }

    .dswp-nav-mobile-menu-icon-text {
        font-size: 1rem;
        margin-left: 8px;
    }

    // Hamburger bars
    .dswp-nav-mobile-bar {
        transition: all 0.3s ease;
        stroke: currentColor;
        stroke-linecap: round;
    }

    // Animation classes for hamburger
    .dswp-nav-mobile-menu-top-bar-open {
        transform: translate(0, 7px) rotate(45deg);
        transform-origin: center;
    }

    .dswp-nav-mobile-menu-middle-bar-open {
        opacity: 0;
        transform: scaleX(0);
    }

    .dswp-nav-mobile-menu-bottom-bar-open {
        transform: translate(0, -5px) rotate(-45deg);
        transform-origin: center;
    }
}

.dswp-block-navigation__container {
    display: flex;
    align-items: center;
    gap: 1rem;
    list-style: none;

    a {
        text-decoration: none;
        padding: 0.5rem 1rem;
        display: block;
        color: inherit;
        opacity: 1;
    }

    .wp-block-navigation-submenu {
        display: flex;
        flex-direction: column;
        position: relative;
        
        .wp-block-navigation__submenu-container {
            display: none;
            opacity: 1;
            visibility: visible;
            transform: none;
            position: static;
            box-shadow: none;
            padding-left: 1.5rem;
        }

        > .wp-block-navigation-item__content {
            display: flex;
            align-items: center;
            gap: 0.5rem;

            &::after {
                content: "";
                width: 0.8em;
                height: 0.8em;
                border-right: 2px solid currentColor;
                border-bottom: 2px solid currentColor;
                transform: rotate(-45deg);
                transition: transform 0.3s ease;
            }
        }

        &.is-open {
            > .wp-block-navigation-item__content::after {
                transform: rotate(45deg);
            }

            > .wp-block-navigation__submenu-container {
                display: block !important;
                pointer-events: auto;

                // Reset nested submenus when parent is closed
                .wp-block-navigation-submenu:not(.is-open) > .wp-block-navigation__submenu-container {
                    display: none !important;
                    pointer-events: none;
                }
            }
        }

        &:not(.is-open) {
            > .wp-block-navigation__submenu-container {
                display: none !important;
                pointer-events: none;
            }

            > .wp-block-navigation-item__content::after {
                transform: rotate(-45deg);
            }
        }
    }

    // Remove all hover effects more aggressively
    .wp-block-navigation-submenu {
        &:hover > .wp-block-navigation__submenu-container,
        &:focus-within > .wp-block-navigation__submenu-container,
        &:focus > .wp-block-navigation__submenu-container {
            opacity: 1;
            visibility: visible;
            transform: none;
            display: none !important; // Force override
            pointer-events: none; // Prevent hover interactions
        }

        > .wp-block-navigation__submenu-container {
            display: none !important; // Force override
            pointer-events: none; // Prevent hover interactions
            
            &:hover,
            &:focus-within,
            &:focus {
                display: none !important; // Force override
            }
        }

        &.is-open > .wp-block-navigation__submenu-container {
            display: block !important; // Show only when explicitly opened
            pointer-events: auto; // Re-enable interactions when open
        }
    }

    // Mobile adjustments
    @media (max-width: 768px) {
        &.is-menu-open {
            flex-direction: column;
            align-items: flex-start;

            .wp-block-navigation-submenu {
                width: 100%;

                .wp-block-navigation__submenu-container {
                    position: static;
                    opacity: 1;
                    visibility: visible;
                    transform: none;
                    padding-left: 1rem;
                    box-shadow: none;
                    background: transparent;
                }
            }
        }
    }
}

// Mobile styles - only apply when menu is open
.dswp-block-navigation-is-mobile-overlay,
.dswp-block-navigation-is-always-overlay {
    .dswp-block-navigation__container.is-menu-open {
        display: grid;
        grid-auto-flow: column;
        grid-template-rows: repeat(10, auto);
        position: absolute;
        left: 0;
        width: 100%;
        background: white;
        z-index: 1000;
        padding: 1rem;
        box-sizing: border-box;
        
        // Force items into columns of 10
        li {
            break-inside: avoid;
            display: block;
        }
    }

    @keyframes slideDown {
        from {
            opacity: 0;
            transform: translateY(-10px);
        }

        to {
            opacity: 1;
            transform: translateY(0);
        }
    }

    .is-menu-open {
        animation: slideDown 0.2s ease-out;
    }
}

// Accessibility
.dswp-nav-mobile-toggle-icon:focus-visible {
    outline: 2px solid var(--wp-admin-theme-color);
    outline-offset: 2px;
    border-color: var(--wp-admin-theme-color);
}

// Animation
@keyframes slideDown {
    from {
        opacity: 0;
        transform: translateY(-20px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.is-menu-open {
    animation: slideDown 0.3s ease-out;
}

// Show toggle icon when "always" overlay is selected
.dswp-block-navigation-is-always-overlay {
    .dswp-nav-mobile-toggle-icon {
        display: flex;
    }
}

// Show toggle icon on mobile devices
@media (max-width: 768px) {
    .dswp-block-navigation-is-mobile-overlay {
        .dswp-nav-mobile-toggle-icon {
            display: flex;
        }
    }
}